.blockMenu {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: calc(100% - 100px);
}
.searchLeft {
  display: block;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  width: 30px;
  height: 30px;
}
.menuRightBlock {
  position: relative;
  display: flex;
  grid-gap: 20px;
  margin-left: auto;
  margin-right: 0px;
}
.logo {
  position: relative;
  width: 100px;
  height: 100px;
  justify-self: center;
  align-self: center;
}
.logo img {
  position: relative;
  width: 100%;
  height: 100%;
}
.menuRightBlock button{
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
  width: 30px;
  height: 30px;
}
.blockMenu button img{
  position: relative;
  height: 100%;
}
.basket_icon {}
.profile_icon {}
.searchRight {
  display: none;
  position: relative;
}

@media (max-width: 800px) {
  .blockMenu {
    grid-template-columns: 1fr 1fr;
    width: 90%;
  }
  .searchLeft {
    display: none;
  }
  .logo {
    position: relative;
    width: 100px;
    height: 100px;
    justify-self: start;
    align-self: center;
  }
  .menuRightBlock {
    position: relative;
    display: flex;
    grid-gap: 20px;
    justify-self: end;
    align-items: center;
  }
  .searchRight {
    display: block;
  }
}