.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}
.navBlock {
  position: relative;
  display: flex;
  grid-gap: 30px;
  margin: 40px 0 20px;
}
.navBlock a {
  font-size: 130%;
  font-weight: 500;
  white-space: nowrap;
}
.long {
  position: relative;
  width: 100%;
  height: 1px;
  background: #c4a370;
  border-radius: 50%;
  opacity: 0.5;
}