.wrapper {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}
.block {
  width: calc(100% - 100px);
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px 30px;
}
@media (max-width: 800px) {
  .block {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 500px) {
  .block {
    grid-template-columns: 1fr;
    width: 85%;
    grid-gap: 20px 30px;
  }
}
